body {
    background-color: #aec4ba;
    font-family: Verdana;
    text-align: center;
}
form {
    background-color: #fff;
    max-width: 500px;
    margin: 50px auto;
    padding: 30px 20px;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.5);
}
.form-control {
    text-align: left;
    margin-bottom: 25px;
}
.form-control label {
    display: block;
    margin-bottom: 10px;
}
.form-control input,
.form-control textarea {
    border: 1px solid #777;
    border-radius: 2px;
    font-family: inherit;
    padding: 10px;
    display: block;
    width: 95%;
}
.forminline{
    display: inline-flex;
}
.forminline div{
    margin-left: 50px;
    margin-right: 50px;
}
.P{
    background-color: #3b47e6;
    border: 1px solid #777;
    border-radius: 10px;
    font-family: inherit;
    font-size: 21px;
    display: block;
    width: 50%;
    margin-top: 50px;
    margin-bottom: 20px;
}
